@import url('https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:ital,wght@0,200..800;1,200..800&display=swap');

body {
    font-family: "Plus Jakarta Sans", sans-serif;
}

p {
    font-size: large;
    color: #484848;
}

h4 {
    font-size: 22px;
    font-weight: bold;
}

h3 {
    font-size: 26px;
    font-weight: bold;
}

.heading {
    font-weight: bold;
}

h2 {
    font-size: 39px;
    font-weight: bold;
    line-height: 1.3;
}

.sticky1 {
    position: fixed;
    width: 100%;
    border-bottom: 2px solid white;
    background-color: transparent;
    z-index: 7;
    transition: all .8s;
    padding: .87rem 0;
}

.sticky {
    background-color: white;
    z-index: 7;
    box-shadow: 0 -5px 30px gray;
}

.navbar-brand img {
    width: 150px;
    position: relative;
    z-index: 11;
}

.navbar-brand {
    position: relative;
}

.nav-link:focus {
    color: white;
}

.sticky .nav-link:focus {
    color: black;
}

.navbar-brand:before {
    content: "";
    position: absolute;
    height: calc(100% + 2rem);
    top: -1rem;
    bottom: 0;
    left: -9800px;
    right: 0;
    border-left: 9999px solid white;
    transform: skewX(-25deg);
}

.navbar-toggler:focus {
    box-shadow: unset;
}

.navbar-toggler {
    border: 1px solid orange;
    border-radius: 50%;
    padding: 10px;
    z-index: 12;
    height: 42px;
}

.icon-bar {
    display: block;
    width: 20px;
    height: 2px;
    border-radius: 1px;
    position: relative;
    background-color: #fab02e;
    margin-bottom: 4px;
    transition: all .5s;
}

.sticky1 ul li {
    margin-left: 25px;
}

.navbar-expand-lg .navbar-nav .nav-link {
    font-size: 16px;
    font-weight: 400;
    /* color: #fff; */
    line-height: 1.5;
    padding: .85rem 0;
    position: relative;
    white-space: nowrap;
    transition: 0.5s all;
}

.sticky1 ul li a::before {
    content: "";
    position: absolute;
    background-color: white;
    height: 1px;
    width: 100%;
    bottom: 10px;
    left: 0px;
    transform: scaleX(0);
    transform-origin: left;
    transition: all .5s;
}

.sticky ul li a::before {
    background-color: green;
}

.sticky1 ul li a:hover::before {
    transform: scaleX(1);
}

.sticky1 button.mybtn.btn.btn-success {
    min-width: 150px;
    padding: .75rem;
}

.btn {
    border-radius: unset;
}

a {
    text-decoration: none;
}

.my-btn {
    padding: 12.5px 35px;
    color: white;
    width: 240px;
}

.my-btn2 {
    transition: all .5s;
}

.my-btn2:hover {
    bottom: 42px;
}

.btn-hover {
    padding: 12.5px 35px;
    color: white;
    min-width: 150px;
    border: none;
    transform: translateY(0);
    transition: all .5s;
}


.btn-hover:hover {
    transform: translateY(-5px);
}

.sticky1 ul li a,
.sticky1 ul li a:hover {
    color: white;
}

.sticky ul li a,
.sticky ul li a:hover {
    color: black;
}

.banner {
    height: 100%;
    width: 100%;
    background-size: cover;
    background-position: center;
    color: white;
    padding: 200px 0;
    text-align: center;
    position: relative;
    z-index: 0;
}

.banner::before {
    content: "";
    position: absolute;
    background: #0000006b;
    height: 100%;
    width: 100%;
    z-index: -1;
    top: 0;
    left: 0;
}

.banner h1 {
    font-weight: bold;
    font-size: 60px;
    font-weight: bold;
    line-height: 1.25;
    margin-bottom: 15px;
}

.banner p {
    padding: 0 180px;
    color: white;
    line-height: 1.6;
}

.mybtn,
.mybtn:hover {
    background-color: #1EA341;
}

.mybtn1 {
    background-color: #1EA341;
}

.mybtn2 {
    background-color: #1EA341;
    display: inline-block;
    text-align: center;
    color: white;
}

.mybtn4,
.mybtn4:hover {
    background-color: #0F57A0;
}

.mybtn5,
.mybtn5:hover {
    background-color: #FAB02E;
}

.inside-banner a {
    display: inline-block;
    width: auto;
}

.items {
    background-color: white;
    padding: 60px 30px;
    text-align: center;
    box-shadow: 0px 0px 26px 10px #0000001a;
    height: 100%;
    padding-bottom: 75px;
}

.items h4 {
    font-weight: bold;
}

.items p {

    padding: 10px 0 30px 0;
    line-height: 1.6;
}

.items a {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 35px;
    margin: auto;
}

.position {
    position: relative;
    top: -75px;
}

.item1 {
    position: relative;
    border-top: 6px solid #1EA341;
}

.item2 {
    position: relative;
    border-top: 6px solid #0F57A0;
}

.item3 {
    position: relative;
    border-top: 6px solid #FAB02E;
}


.title {
    font-size: large;
    margin-bottom: 20px;
    color: white;
    padding: 8px 20px;
    background-color: #FAB02E;
    width: 270px;
    text-transform: uppercase;
    text-align: center;
}

.info3 .title {
    margin: auto;
    margin-bottom: 20px;
}

.main-item1 {
    padding-right: 46px;
}

.main-item1 p {
    line-height: 1.6;
}

.main-item1 h2 {
    margin-top: 15px;
}

.main-item2 img {
    width: 100%;
    box-shadow: 0px 0px 26px 10px #0000001a;
}

.inside-info2 {
    padding-left: 30px;
}

.inside-info2 .btn {
    font-size: 12px;
    margin-bottom: 7px;
    color: white;
    padding: 5px 20px;
    background-color: #FAB02E;
}

.inside-info2 h3 {
    margin-top: 30px;
    font-size: 26px;
    font-weight: normal;
}

.info3 {
    text-align: center;
}

.info3 .btn {
    font-size: 12px;
    margin-bottom: 7px;
    color: white;
    padding: 5px 20px;
    background-color: #FAB02E;
}

.info3 p {
    padding: 0 280px;
}

.inside-info4 {
    padding-top: 3rem;
}

.inside-info4 h2 {
    line-height: 1.30;
}

.inside-info4 p {
    padding-right: 110px;
}

.graph-sec p {
    padding: 0 210px;
}

.graph-sec img {
    width: 70%;
    margin: 0 auto;
    display: block;
    margin-bottom: 40px;
    margin-top: 40px;
}

.graph-sec strong {
    font-size: 40px;
    text-align: center;
}

.card {
    height: 100%;
    width: 100%;
    cursor: pointer;
    border: unset;
    border-radius: unset;
    box-shadow: 0px 0px 26px 10px #0000001a;
}

.card img {
    cursor: pointer;
    transition: all .5s;
    width: 100%;
    height: 260px;
}

.card-pic {
    padding: 20px 20px 0 20px;
}

.card-pic2 {
    overflow: hidden;
    position: relative;
}

.card-pic2 img {
    border-radius: unset;
}

.card-body {
    padding: 0 20px 20px 20px;
}

.card-text {
    line-height: 1.6;
}

.card img:hover {
    transform: scale(1.1);
}

.card-body h4 {
    margin: 0;
    padding: 14px 0;
    min-height: 85px;
}

.date {
    position: absolute;
    background-color: #FAB02E;
    padding: 0 10px;
    color: white;
    right: 0;
    bottom: 0;
    font-size: 18.5px;
    font-weight: 400;
    text-transform: uppercase;
}

.last-sec {
    position: relative;
}

.last-sec::before {
    content: "";
    position: absolute;
    background: #F2F2F2;
    width: 100%;
    height: calc(100% - 7rem);
    z-index: -1;
    top: 0;
}

.spacing {
    margin-bottom: 70px;
}

.footer-image {
    padding-bottom: 30px;
}

.footer-item1 a,
.footer-item2 a {
    text-decoration: none;
    color: black;
    padding: 0;
    font-size: large;
}

.footer-item1 a:hover,
.footer-item2 a:hover {
    color: green;
}


.copyright {
    padding-bottom: 1rem;
    font-size: large;
    line-height: 1.6;
}

.copyright a {
    color: #484848;
}

.copyright img {
    width: 30px;
    margin-left: 4px;
}

.footer-item1,
.footer-item2 {
    position: relative;
    margin-bottom: 16px;
}

.footer-item1::before {
    content: "";
    position: absolute;
    height: 2px;
    width: 40px;
    background-color: #1EA341;
    top: -12px;
    left: 13px;
}

.footer-item1 address,
.footer-item1 a {
    line-height: 1.6;
    font-size: large;
}

.footer-item2 ul li a {

    line-height: 1.6;
    padding-bottom: 7px;
}

.footer-item1 h3 {
    margin-bottom: 16px;
}

.footer-item2 h3 {
    margin-bottom: 16px;
}

.footer-item2::before {
    content: "";
    position: absolute;
    height: 2px;
    width: 40px;
    background-color: #FAB02E;
    top: -12px;
    left: 13px;
}

footer {
    background-color: #F2F2F2;
    padding-top: 64px;
}

.footer-text {
    line-height: 1.6;
}

.footer-logo {
    padding-right: 40px;
}

.footer-logo2 {
    padding-left: 30px;
}

.video-box {
    padding: 0;
    position: relative;
}

.video-box::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: #0000006b;
}

.video-button {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    cursor: pointer;
}

.video-button img {
    width: 60px;
}

.modal-content {
    padding: 13px;
    background-color: transparent;
    border: unset;
}

.cross {
    height: 28px;
    width: 28px;
    border-radius: 50% 50%;
    background-color: orange;
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.section3 {
    padding-top: 50px;
    overflow: hidden;
    position: relative;
}

section.spacing.section3:before {
    content: "";
    position: absolute;
    background: #F2F2F2;
    height: 85%;
    width: 60%;
    top: -60px;
    right: 0;
    z-index: -1;
}

.btn-hover2 {
    margin-right: 10px;
    margin-left: auto;
    display: none;
    font-size: 14px;
}

.copyright2 {
    font-size: 14px;
    padding-top: 1rem;
    display: none;
    color: #484848;
}

