@media (max-width: 1199px) {
    p {
        font-size: large;
    }

    .banner p {
        padding: 0 100px;
    }

    .sticky1 ul li {
        margin-left: 14px;
    }

    .graph-sec p {
        padding: 0;
    }

    .main-item1 {
        padding-right: 0;
    }

    .main-item1 p {
        padding-right: 30px;
        line-height: 1.6;
        /* font-size: large; */
    }

    .inside-info2 h3 {
        font-weight: normal;
    }

    .inside-info2 {
        padding-left: 0;
    }

    .card-body h4 {
        min-height: 85px;
    }
}

@media (max-width: 991px) {

    .navbar-collapse {
        position: absolute;
        background: white;
        width: 100%;
        right: -100%;
        top: 0px;
        z-index: 10;
        height: 100vh;
        padding: 100px 20px 20px;
        /* opacity: 1; */
        transition: all .4s ease-in-out;
    }

    .navbar-expand-lg .navbar-nav .nav-link {
        font-size: 20px;
    }

    .sticky1 ul li a,
    .sticky1 ul li a:hover {
        color: black;
    }

    .sticky1 ul {
        height: calc(100vh - 200px);
    }

    .btn-hover2 {
        display: inline-block;
    }

    .temp-btn {
        display: none;
    }

    h2 {
        font-size: 35px;
    }

    h3 {
        font-size: 24px;
    }

    p {
        font-size: 16px;
    }

    .banner h1 {
        font-size: 45px;
    }

    .banner p {
        padding: 0;
    }

    .main-item1 {
        margin-bottom: 55px;
    }

    .graph-sec img {
        width: 100%;
    }

    .spacing {
        margin-bottom: 40px;
    }

    .section3 {
        padding-top: 0;
    }

    .graph-sec strong {
        font-size: 30px;
    }

    .inside-info2 {
        padding: 30px 0;
    }

    .inside-info2 h2 {
        font-size: 35px;
    }

    .info3 p {
        padding: 0;
    }

    .inside-info4 p {
        padding-bottom: 30px;
    }

    .footer-item1,
    .footer-item2 {
        margin-top: 25px;
    }

    .footer-item1 a,
    .footer-item2 a {
        font-size: 16px;
        line-height: 1.5;
    }

    .footer-item1 address {
        font-size: 16px;
    }

    .line {
        margin: 0;
    }

    .copyright {
        font-size: 16px;
        padding-top: 1rem;
    }

    .copyright2 {
        font-size: 14px;
        display: block;
        margin-left: 14px;
    }
}

